import {uniPopup} from '@dcloudio/uni-ui'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
		}
	},
	props: {
		couponType: {
			type: String | Number,
			default: 0
		},
		detail: {
			type: Array
		}
	},
	methods: {
		goProduct(item) {
			uni.navigateTo({
				url: '/pages/product/product?id=' + item.id
			})
		},
		goCategory(item) {
			uni.navigateTo({
				url: '/pages/product/list?cat_id=' + item.id
			})
		},
		open() {
			this.$refs.$popup.open()
		},
		close() {
			this.$refs.$popup.close()
		}
	}
}
